<template>
    <div class="panel">
        <b-table :items="userlist" show-empty>
            <template #empty="">
                <p align="center">
                    Nothing<br /><br />
                    <b-button variant="info" to="/user_add"
                        >點選來添加人員</b-button
                    >
                </p>
            </template>
        </b-table>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    data() {
        return {
            fields: [
                { key: "user_id", label: "id", sortable: true },
                { key: "user_name", label: "名字", sortable: false },
            ],
            emptyStr: "<p align='center'>Nothing</p>",
        };
    },
    computed: {
        ...mapState({
            users: (state) => state.users,
            items: (state) => state.items,
            borrows: (state) => state.borrows,
        }),
        userlist() {
            if (!this.users) return [];
            return Array.from(Object.entries(this.users), ([key, value]) =>
                Object.assign(value, { id: key })
            );
        },
    },
};
</script>

<style scoped>
.panel {
    margin: 12px 24px 12px 24px;
}
</style>