<template>
    <div class="panel w-50">
        <b-input class="mt-3" v-model="user.sid" placeholder="輸入人員id" />
        <b-input class="mt-3" v-model="user.realname" placeholder="輸入姓名" />
        <b-input class="mt-3" v-model="user.username" placeholder="輸入Username" />
        <b-input class="mt-3" v-model="user.email" placeholder="輸入Email" />
        <b-input class="mt-3" type="password" v-model="user.password" placeholder="輸入密碼" />
        <b-input class="mt-3" type="password" v-model="user.repassword" placeholder="重覆輸入密碼" />
        <b-form-select class="mt-3" v-model="user.role" :options="options"></b-form-select>
        <p align="center">
            <b-button class="mt-3" variant="info" @click="addUser">送出</b-button>
        </p>
    </div>
</template>

<script>
import { fireDb, fireAuth } from '@/plugins/firebase'
export default {
    data() {
        return {
            user: {
                sid: '',
                realname: '',
                username: '',
                email: '',
                password: '',
                repassword: '',
                role: 'user',
            },
            options: [
                { value: 'user', text: '使用者' },
                { value: 'manager', text: '管理者' },
            ],
        }
    },
    methods: {
        checkValid() {
            if (this.user.realname === ''||this.user.username === ''||this.user.sid === '') {
                alert("Name/SID should not be empty!")
                return false
            }
            if (this.user.password.length < 6) {
                alert("Password should be at least 6 chars!")
                return false
            }
            if (this.user.password === '') {
                alert("Password should not be empty!")
                return false
            }
            if (this.user.password !== this.user.repassword) {
                alert("Password should be same!")
                return false
            }
            return true
        },
        async addUser() {
          if (!this.checkValid()) return
            try {
                let userCredential = await fireAuth.createUserWithEmailAndPassword(this.user.email, this.user.password)
                await fireDb.collection('users').doc(userCredential.user.uid).set({
                    sid: this.user.sid,
                    realname: this.user.realname,
                    username: this.user.username,
                    email: this.user.email,
                    role: this.user.role,
                })
                alert("Write new user to database!")
                // console.log("Response", response)
            } catch(e) {
                console.log("Error", e)
            }
        },
    },
}
</script>
    
<style scoped>
.panel {
    margin: 12px auto 12px auto;
}
</style>