<template>
    <div class="panel">
        <b-table :items="itemlist" :fields="fields" show-empty striped>
            <template #empty="">
                <p align="center">
                    Nothing<br /><br />
                    <b-button variant="info" to="/item_add">Add Item</b-button>
                </p>
            </template>
            <template v-slot:cell(actions)="row">
                <b-button-group>
                    <b-button
                        size="sm"
                        variant="outline-primary"
                        @click="editItem(row.item)"
                    >
                        <b-icon icon="pen" />
                    </b-button>
                    <b-button
                        size="sm"
                        variant="outline-primary"
                        @click="deleteItem(row.item)"
                    >
                        <b-icon icon="trash" />
                    </b-button>
                </b-button-group>
            </template>
        </b-table>
        <b-modal v-model="showEdit" size="lg" title="修改物品" @ok="updateItem">
            <b-input class="mt-3" v-model="item.name" placeholder="物品名稱" />
            <b-input class="mt-3" v-model="item.price" placeholder="價格" />
            <b-input class="mt-3" v-model="item.year" placeholder="製造年份" />
            <b-input class="mt-3" v-model="item.note" placeholder="備註" />
        </b-modal>

    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    data() {
        return {
            fields: [
                { key: "name", label: "物品名稱", sortable: true },
                { key: "price", label: "價格", sortable: true },
                { key: "year", label: "年份", sortable: true },
                { key: "note", label: "備註", sortable: false },
                { key: "updated", label: "更新時間", sortable: true },
                { key: "actions", label: "動作", sortable: false },
            ],
            emptyStr: "<p align='center'>Nothing</p>",
            itemlist: [],
            showEdit: false,
            item: {
                name: '',
                price: null,
                year: null,
                note: '',
                updated: '',
            },
        };
    },
    computed: {
        ...mapState({
            users: (state) => state.users,
            items: (state) => state.items,
            borrows: (state) => state.borrows,
        }),
    },
    created() {
        this.updateItems()
    },
    methods: {
        getToday() {
            var today = new Date();
            var dd = String(today.getDate()).padStart(2, "0");
            var mm = String(today.getMonth() + 1).padStart(2, "0"); //January is 0!
            var yyyy = today.getFullYear();
            return yyyy + mm + dd;
        },
        async updateItem() {
            this.item.updated = this.getToday()
            let newItem = Object.assign({}, this.item)
            delete newItem.id
            await this.$store.dispatch('update_item', {
                collection: 'items',
                id: this.item.id,
                value: newItem,
            })
            this.updateItems()
        },
        obj2list(myobj) {
            let result = []
            for (let key in myobj) {
                console.log("KEY", key)
                let item = myobj[key]
                console.log("ITEM", item)
                item.id = key
                result.push(item)
            }
            console.log("RESULT", result)
            return result
        },
        updateItems() {
            if (!this.items) {
                this.itemlist = []
            } else {
                this.itemlist = this.obj2list(this.items)
            }
        },
        editItem(itemData) {
            Object.assign(this.item, itemData)
            this.showEdit = true
        },
        async deleteItem(itemData) {
            console.log("iD", itemData)
            let yn = confirm('確定要刪除嗎？')
            if (yn) await this.$store.dispatch('delete_item', {
                collection: 'items',
                id: itemData.id,
            })
            this.updateItems()
        },
    },
};
</script>
  
<style scoped>
.panel {
    margin: 12px 24px 12px 24px;
}
</style>